.menu-list{
    width: 224px;
    .menu-item{
        margin-left: 0px;
        width: 100%;
        border-radius: 0;
        justify-content: flex-start;
        padding: 10px 16px;
        font-weight: 400;
        &:hover{
            background-color: rgba(22, 24, 35, 0.03);
        }
    }
}

.menu-popper{
    padding-bottom: 8px;
}

.header{
    position: relative;
    height: 50px;
    margin-top: -8px;
}
.back-btn{
    width: 50px;
    height: 100%;
    cursor: pointer;
    background-color: transparent;
}
.title{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}

.separate{
    border-top: 1px solid rgba(22, 24, 35, 0.12);
}


